<template>
  <div id="loading-container" class="row hover vs-con-loading__container">
    <div class="alert-container" v-if="alertActive">
      <vs-alert :active.sync="alertActive" :color="alertVariant" closable icon-pack="feather" close-icon="icon-x" class="mb-5">
        <p>{{ alertMessage }}</p>
      </vs-alert>
    </div>

    <nv-card class="mb-base">
      <h4 class="mb-3">{{ $t('PERSONAL_INFORMATION') }}</h4>
      <div class="flex flex-wrap-reverse items-center mb-base">
        <vs-button class="p-3 mr-4" @click="modify" color="primary" type="filled" icon="fa-pen" icon-pack="far">{{ $t('MODIFY') }}</vs-button>
        <vs-button class="p-3 mr-4" @click="changePassword" color="primary" type="filled" icon="fa-key" icon-pack="far">{{ $t('CHANGE_PASSWORD') }}</vs-button>
      </div>
      <div>
        <p class="info-row">
          <span>{{ $t('SALUTATION') }}:</span>
          <span>{{ user.salutation | placeholder('-') }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('FIRST_NAME') }}:</span>
          <span>{{ user.firstName | placeholder('-') }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('MIDDLE_NAME') }}:</span>
          <span>{{ user.middleName | placeholder('-') }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('LAST_NAME') }}:</span>
          <span>{{ user.lastName | placeholder('-') }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('SUFFIX') }}:</span>
          <span>{{ user.suffix | placeholder('-') }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('MOTHERS_MAIDEN_NAME') }}:</span>
          <span>{{ user.mothersMaidenName | placeholder('-') }}</span>
        </p>
      </div>
    </nv-card>

    <nv-card class="mb-base">
      <h4 class="mb-3">{{ $t('ACCOUNT_INFORMATION') }}</h4>
        <div class="row hover">
          <p class="info-row">
            <span>{{ $t('USERNAME') }}:</span>
            <span>{{ $store.state.user.username | placeholder('-') }}</span>
          </p>
          <p class="info-row">
            <span>{{ $t('LAST_LOGIN_DATE') }}:</span>
            <span>{{ user.lastLoginDate | formatUnixDate }}</span>
          </p>
      </div>
    </nv-card>

    <nv-card class="mb-base">
      <h4 class="mb-3">{{ $t('PREFERENCES') }}</h4>
        <div class="flex justify-between">
          <span>{{ $t('OTP_CHANNEL') }}:</span>
          <ul class="centerx flex">
            <li>
              <vs-radio v-model="otpChannel" vs-value="SMS" @change="updateOtpChannel">{{ $t('SMS') }}</vs-radio>
            </li>
            <li>
              <vs-radio v-model="otpChannel" vs-value="EMAIL" @change="updateOtpChannel">{{ $t('EMAIL') }}</vs-radio>
            </li>
            <li>
              <vs-radio v-model="otpChannel" vs-value="BOTH" @change="updateOtpChannel">{{ $t('BOTH') }}</vs-radio>
            </li>
          </ul>
        </div>
    </nv-card>
  </div>
</template>

<script>
import AlertMixin from '@/mixins/commons/AlertMixin'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import SecurityService from '@/services/nucleus/SecurityService'

export default {
  name: 'profile-view',
  mixins: [AlertMixin, LoadingMixin],
  data: function () {
    return {
      user: {},
      otpChannel: 'SMS'
    }
  },
  methods: {
    async load () {
      let self = this
      this.startLoading('loading-container')
      SecurityService.profile({
        userId: this.$store.state.user.userId
      }).then(function (response) {
        self.stopLoading('loading-container')
        self.user = response.data || {}
      })

      SecurityService.preference({}).then(function (response) {
        self.stopLoading('loading-container')
        let data = response.data || {}
        self.otpChannel = data['banqio.otp.channel'] || 'SMS'
      })
    },
    updateOtpChannel () {
      let self = this
      this.startLoading('loading-container')
      SecurityService.updateUserOtpChannel({
        otpChannel: this.otpChannel
      }).then(function (response) {
        self.stopLoading('loading-container')
        self.showSuccessAlert(response.data.messages)
      }).catch(function (error) {
        self.stopLoading('loading-container')
        self.showDangerAlert(error.response.data.errors)
      })
    },
    changePassword () {
      this.$router.push({ name: 'ChangePassword', params: { id: this.$route.params.id } })
    },
    modify () {
      this.$router.push({ name: 'ModifyProfile', params: { id: this.$route.params.id } })
    }
  },
  mounted () {
    this.load()
  }
}
</script>
